.primaryButton {
  cursor: pointer;
  border-radius: 8px;
  margin-top: 2px;
  max-width: 100%;
  width: 300px;
  text-align: center;
  height: 36px;
  padding: 0 16px;
  color: var(--white---white);
  background-color: #447bba;
  border: none;
  font-size: 15px;
  font-weight: 500;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
